@mixin pseudo($display: block, $pos: absolute, $content: "") {
  content: $content;
  display: $display;
  position: $pos;
}

@mixin list-none($list-style: none, $margin: 0, $padding: 0) {
  list-style: $list-style;
  margin: $margin;
  padding: $padding;
}

@mixin push--auto {
  margin: {
    left: auto;
    right: auto;
  }
}

@mixin container {
  padding: {
    left: $padding;
    right: $padding;
  }

  @media (min-width: $desktop-width) {
    padding: {
      left: $padding-desktop;
      right: $padding-desktop;
    }
  }
}

@mixin clear-margin($mt: 0, $mb: 0) {
  margin: {
    top: $mt;
    bottom: $mb;
  }
}
