*,
*::before,
*::after {
  box-sizing: border-box;
}

html {
  height: 100%;
}

body {
  min-width: 320px;
  min-height: 100%;
  margin: 0;
  padding: 0;
  font-family: $font;
  font-size: 14px;
  font-style: normal;
  font-weight: normal;
  line-height: 1.3;
  color: $basic-black;
}

img {
  max-width: 100%;
  height: auto;
}

a {
  text-decoration: none;
}
